import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Nav from './components/Nav';
import './App.css';
import { Main } from './Pages/index';

function App() {
  return (
    <>
      <Router>
        <Nav />
        <Switch>
          <Route path='/' component={Main} />
        </Switch>
      </Router>
    </>
  );
}

export default App;
